<template>
    <page classes="d-flex flex-column">
        <main-header @menu="fixed = !fixed; open = !open" :menu="open" />
        <div class="d-flex flex-row position-relative h-100">
            <div class="sidebar" @mouseenter="openMenu()" @mouseleave="closeMenu()">
                <!--
                <router-link class="logo" to="/inbox">
                    <strong @click="fixed = !fixed"><span v-if="!closed">Mercatorum</span><span class="font-weight-bolder crm">CRM</span></strong>
                </router-link>
                -->
                <aside class="bg-white border-right sidebar-content">
                    <sidebar />
                </aside>
            </div>
            <main :class="mainClass">
                <div class="position-relative h-100 content">
                    <fade-in-down-out-up>
                        <router-view :key="routeKey"></router-view>
                    </fade-in-down-out-up>
                </div>
            </main>
        </div>
        <!--<chat />-->
    </page>
</template>

<script>
    import Page from './layout/Page'
    import MainHeader from './layout/MainHeader'
    import Sidebar from './layout/Sidebar'
    import FadeInDownOutUp from './transitions/FadeInDownOutUp'
    import Chat from './Chat'
    import services from '../services'

    import { mapGetters, mapActions, mapState } from 'vuex'

    export default {
        name: 'home',
        components: {Page, Sidebar, MainHeader, FadeInDownOutUp, Chat},
        methods: {
            ...mapActions('auth', {
                getUser: 'getUser'
            }),
            openMenu: function () {
                if (this.open) return false
                this.$options.openTimeout = setTimeout(() => {
                    this.closed = false
                }, this.$options.openDelay)
            },
            closeMenu: function () {
                clearTimeout(this.$options.openTimeout)
                if (this.open) return false
                this.closed = true
            }
        },
        data () {
            return {
                open: true,
                closed: true,
                fixed: false
            }
        },
        openDelay: 400,
        openTimeout: null,
        computed: {
            sidebarClass () {
                return {
                    sidebar: true,
                    closed: this.closed && !this.open,
                    fixed: this.fixed,
                    flex: !this.fixed
                }
            },
            mainClass () {
                return {
                    'd-flex': true,
                    'flex-column': true,
                    'flex-grow-1': true,
                    'position-relative': true,
                    margin: this.fixed
                }
            },
            routeKey () {
                return this.basePath
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            }
        },
        mounted () {
            this.getUser()
            //services.ws.open()
        }
    }
</script>
<style scoped>
    main.margin {
        margin-left: 45px;
    }
    main {
        margin-left: 0;
    }
    .sidebar.fixed {
        position: fixed;
        z-index:9999;
        box-shadow: 1px 0px 20px 0px #ccc;
    }
    .sidebar.fixed.closed {
        box-shadow: none;
    }
    .sidebar.flex {
        position: relative;
    }
    .sidebar {
        transition: all .4s ease-out;
        overflow-y: auto;
        position: relative;
        width: 300px;
    }
    .sidebar-content {
        position: absolute;
        top: 0; left:0; right: 0; bottom: 0;
        padding-bottom: 50px;
    } 
    .sidebar-wrapper {
        flex-direction: column;
        display: flex;
        width: 300px;
        height: 100%;
        overflow: hidden;
    }
    .sidebar.closed .crm {
        font-size: 13px;
    }
    .sidebar.closed {
        width: 47px;
        transition: all .4s ease-out;
    }

    .sidebar.closed >>> .text {
        display: none !important;
    }

    .sidebar.closed >>> li a {
        width: 0;
    }

    .sidebar.closed >>> .menu-title {
        opacity: 0;
        transition: all .5s ease;
    }

    
    .wrapper {
        height: 100%;
    }
    /*
    .logo {
        display: block;
        position: relative;
        top: 0;
        left: 0;
        height: 60px;
        font-size: 20px;
        line-height: 60px;
        text-align: center;
        font-weight: 300;
        color: #fff;
        background-color: #6c2224;
    }
    */
    
    .content {
        overflow-y: auto;
        left:0;
        right:0;
        top: 0;
    }
</style>
<style>
    .sidebar::-webkit-scrollbar {
        width: 4px;
    }
    
    .sidebar:hover::-webkit-scrollbar-thumb {
        background: rgba(108,34,36, .5);
    }

    .content::-webkit-scrollbar {
        width: 4px;
    }

    .content:hover::-webkit-scrollbar-thumb {
        background: rgba(108,34,36, .5);
    }

    ::-webkit-scrollbar-track {
        background: rgba(255, 255, 255, 0); 
    }
    
    ::-webkit-scrollbar-thumb {
        background: rgba(108,34,36, .1);
        border-radius: 20px;
    }

    ::-webkit-scrollbar-thumb:hover {
        background: rgba(108,34,36, 1) !important;
    }
</style>