<template>
    <div class="row mb-3 no-print">
        <div class="col-6">
            <ui-button
                v-if="own && status == 3"
                @click="$emit('worked')"
                size="sm"
                color="success"
                icon="fas fa-lock"
                :loading="loading.worked"
                :disabled="loading.worked"
            >SEGNA COME "LAVORATA"</ui-button>
            <ui-button
                v-if="own && status !== 3"
                @click="$emit('work')"
                size="sm" icon="fas fa-lock-open" color="primary"
                :loading="loading.towork"
                :disabled="loading.towork"
            >SEGNA COME "IN LAVORAZIONE"</ui-button>
            <ui-button
                v-if="own && status !== 6"
                @click="$emit('trash')"
                size="sm" icon="far fa-trash-alt" color="dark"
                :loading="loading.trash"
                :disabled="loading.trash"
            >SPOSTA IN CESTINO</ui-button>
        </div>
        <div class="col-6 text-right">
            <ui-button
                v-if="!assigned" 
                @click="$emit('assign')"
                color="success"
                size="sm"
                icon="far fa-hand-point-right"
                :disabled="this.loading.assign"
                :loading="this.loading.assign"
            >PRENDI IN CARICO</ui-button>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'request-toolbar',
        props: {
            loading: {
                type: Object,
                default: {
                    assign: false,
                    trash: false,
                    worked: false,
                    towork: false
                },
                required: false
            },
            status: {
                type: Number,
                required: true
            },
            own: {
                type: Boolean,
                required: false,
                default: false
            },
            assigned: {
                type: Boolean,
                required: false,
                default: false
            }
        }
    }
</script>