<template>
    <div class="row">
        <div class="col">
            <div class="card rounded-0">
                <div class="card-body p-3">
                    <div class="d-flex align-items-center justify-content-between">
                        <div><i class="far fa-envelope fa-4x text-muted"></i></div>
                        <div class="text-right">
                            <p class="card-text mb-0">Richieste</p>
                            <h1 class="card-title m-0"><span id="stats-req-count">{{ requests }}</span></h1>
                            <!-- <span class="d-block text-muted">Tasso di Lavorazione <span id="stats-req-work-rate">65,6</span>%</span> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col">
            <div class="card rounded-0">
                <div class="card-body p-3">
                    <div class="d-flex align-items-center justify-content-between">
                        <div><i class="fas fa-users fa-4x text-muted"></i></div>
                        <div class="text-right">
                            <p class="card-text mb-0">Contatti</p>
                            <h1 class="card-title m-0"><span id="stats-contact-count">{{ contacts }}</span></h1>
                            <!-- <span class="d-block text-muted">Entrate per Contatto <span id="stats-contact-revenue">476,0</span> €</span> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col">
            <div class="card rounded-0">
                <div class="card-body p-3">
                    <div class="d-flex align-items-center justify-content-between">
                        <div><i class="far fas fa-graduation-cap fa-4x text-muted"></i></div>
                        <div class="text-right">
                            <p class="card-text mb-0">Conversioni</p>
                            <h1 class="card-title m-0">0</h1>
                            <!-- <span class="d-block text-muted">Tasso di Conversione <span id="stats-conversion-rate">58,2</span>%</span> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col">
            <div class="card rounded-0 bg-success text-white">
                <div class="card-body p-3">
                    <div class="d-flex align-items-center justify-content-between">
                        <div><i class="fas fa-euro-sign fa-4x"></i></div>
                        <div class="text-right">
                            <p class="card-text mb-0">Entrate stimate</p>
                            <h1 class="card-title m-0">0</h1>
                            <!-- <span class="d-block">Entrate da CRM <span id="stats-revenue-crm">21,3</span>%</span> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div> 
</template>

<script>
    import services from '../../services'
    export default {
        data () {
            return  {
                loading: false,
                requests: 0,
                contacts: 0
            }
        },
        mounted () {
            this.load()
        },
        methods: {
            load () {
                this.loading = true
                services.getAnalyticsWidgets().then(data => {
                    this.requests = data.requests
                    this.contacts = data.contacts
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading = false
                })
            }
        }
    }
</script>

