<template>
    <div class="chat-container" v-if="online">
        <conversation @toggle="toggleChat" v-for="user in openChatUsers" :minimized="user.minimized" :key="user.id" :user="user" :messages="_messages(user.id)" @close="removeChat(user.id)" :more_history="moreHistory(user.id)" />
        <div :class="chatUsersClass">
            <div class="chat-users-header cursor-pointer d-flex" @click="toggle">
                <div class="flex-grow-1">Chat ({{ onlineUsersCount }})</div>
                <div class="text-right">
                    <span :class="badgeClass" v-if="totalUnreadCount">{{ totalUnreadCount }}</span>
                </div>
            </div>
            <div class="chat-users-body">
                <div class="chat-users-list">
                    <div v-for="user in allUsers" class="cursor-pointer d-flex px-2 pt-2" style="line-height:35px;" :key="user.id" @click="openChat(user.id)" v-if="currentUserId != user.id">
                        <img :src="`/uploads/users/${user.photo}`" class="mr-2 mb-2" style="height:35px;width:35px; border-radius:50%">
                        <div class="flex-grow-1">
                            {{ user.first_name }} {{ user.last_name }}
                        </div>
                        <div class="text-right">
                            <span :class="badgeClass" v-if="_unread(user.id) > 0">{{ _unread(user.id) }}</span>
                            <small><i :class="`'online fas fa-circle font-weight-bold mr-2 small ${user.online ? 'text-success' : 'text-secondary'}`"></i></small>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <audio id="audio" ref="notification" src="/uploads/audio/notification.mp3"></audio>
    </div>
</template>
<script>
    import { mapState, mapGetters } from 'vuex'
    import Conversation from './chat/Conversation'
    import services from '../services'
    import LS from '../services/localstorage'
    import store from '../store'
    import Vue from 'vue'

    export default {
        name: 'chat-component',
        components: { Conversation },
        data () {
            return {
                online: false,
                chats: [],
                animation: false,
                minimized: true
            }
        },
        iconTimeout: null,
        computed: {
            ...mapState('chat', {
                messages: 'messages',
                unread_messages: 'unread_messages'
            }),
            ...mapGetters('chat', {
                allUsers: 'allUsers',
                onlineUsers: 'onlineUsers',
                offlineUsers: 'offlineUsers'
            }),
            totalUnreadCount () {
                return this.allUsers.map(u => this._unread(u.id)).reduce((total, current) => total + current, 0)
            },
            chatUsersClass ()
            {
                return {
                    'chat-users': true,
                    'bg-white': true,
                    minimized: this.minimized
                }
            },
            badgeClass ()
            {
                return {
                    badge: true,
                    'badge-danger': true,
                    animated: this.animation,
                    flash: this.animation
                }
            },
            openChatUsers ()
            {
                return this.chatUsers.filter(u => {
                    return this.chats.find(c => c.id === u.id)
                }).map(u => {
                    const index = this.chats.findIndex(c => c.id === u.id)
                    return {
                        ...u,
                        minimized: this.chats[index].minimized
                    }
                })
            },
            chatUsers ()
            {
                return this.allUsers.filter(u => u.id !== LS.get('user').id)
            },
            currentUserId ()
            {
                return LS.get('user').id
            },
            onlineUsersCount ()
            {
                return this.chatUsers.filter(u => u.online).length
            }
        },
        methods: {
            toggleChat (id) {
                const index = this.chats.findIndex(c => c.id === id)
                Vue.set(this.chats, index, {
                    ...this.chats[index],
                    minimized: !this.chats[index].minimized
                })
            },
            toggle ()
            {
                this.minimized = !this.minimized
            },
            moreHistory (id) {
                return this.chats.find(c => c.id === id).more_history
            },
            _messages (id) {
                return this.messages.filter(m => m.from.id == id || m.to == id)
            },
            _unread (id) {
                return this.messages.filter(m => !m.read_at && m.from.id == id && m.from.id != LS.get('user').id).length + (this.unread_messages[id] || 0)
            },
            removeChat (id) {
                this.chats.splice(this.chats.findIndex(c => c.id === id), 1)
            },
            openChat (id) {
                const index = this.chats.findIndex(c => c.id === id)
                if (index > -1) {
                    return Vue.set(this.chats, index, {
                        ...this.chats[index],
                        minimized: false
                    })
                }
                this.chats.push({
                    id,
                    more_history: true,
                    minimized: false
                })
            },
            playNotification () {
                this.$refs.notification.pause();
                this.$refs.notification.currentTime = 0;
                this.$refs.notification.play()
            }
        },
        mounted () {
            services.ws.on('USERS', users => {
                store.commit('chat/set_users', users)
            }).on('USER_CONNECTED', user => {
                console.log('USER_CONNECTED', user)
                store.commit('chat/set_online_user', user)
            }).on('USER_DISCONNECTED', user_id => {
                store.commit('chat/set_offline_user', user_id)
            }).on('CHAT_MESSAGE', message => {
                if (message.from.id !== LS.get('user').id) {
                    this.playNotification()
                    this.animation = true
                    clearTimeout(this.$options.iconTimeout)
                    this.$options.iconTimeout = setTimeout(() => this.animation = false, 600)
                }
                store.commit('chat/add_message', message)
            }).on('HISTORY', ({ id, messages, last_id }) => {
                if (messages.length < 10) {
                    const index = this.chats.findIndex(c => c.id === id)
                    Vue.set(this.chats, index, {
                        id,
                        more_history: false,
                        minimized: this.chats[index].minimized
                    })
                }
                store.commit('chat/history', messages)
            }).on('READ_MESSAGES', payload => {
                store.commit('chat/set_messages_as_read', payload)
            }).on('UNREAD_MESSAGES', messages => {
                store.commit('chat/set_unread_messages_count', messages)
            }).on('authorized', () => {
                services.ws.send('USERS')
                services.ws.send('UNREAD_MESSAGES')
                this.online = true
            }).on('close', () => {
                this.online = false
            })
        }
    }
</script>
<style scoped>
    .chat-users {
        width: 200px;
        -webkit-box-shadow: 3px -3px 7px -3px rgba(0,0,0,0.55);
        -moz-box-shadow: 3px -3px 7px -3px rgba(0,0,0,0.55);
        box-shadow: 3px -3px 7px -3px rgba(0,0,0,0.55);
    }
    
    .chat-users .chat-users-header {
        height: 35px;
        line-height: 15px;
        padding: 10px;
        background: #333;
        color: white;
    }
    .chat-users .chat-users-list {
        height: 205px;
        overflow: hidden;
        transition: all .3s ease;
    }
    .chat-users .chat-users-body {
        height: auto;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .chat-users.minimized .chat-users-list {
        height: 0;
        transition: all .3s ease;
    }
    .chat-users .chat-users-body div {
        
    }
    .chat-container {
        position: fixed;
        bottom: 0;
        right: 0;
        display: flex;
        align-items: flex-end;
        padding: 0 20px;
    }
</style>