import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Login from '../components/Login'
import Inbox from '../components/Inbox'
import Request from '../components/Request'
import PublicRequest from '../components/PublicRequest'
import Contact from '../components/Contact'
import Contacts from '../components/Contacts'
import Users from '../components/Users'
import Calls from '../components/Calls'
import NotFound from '../components/NotFound'
import LS from '../services/localstorage'

Vue.use(Router)
const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/ticket/:token',
            name: 'PublicRequest',
            component: PublicRequest,
            props: true
        },
        {
            path: '/login',
            name: 'Login',
            component: Login
        }, {
            path: '/',
            name: 'Home',
            component: Home,
            children: [
                {
                    path: '/calls/:call_direction/:page?',
                    component: Calls,
                    props: true
                },
                {
                    path: '/users',
                    component: Users
                },
                {
                    path: '/contact/:id',
                    component: Contact,
                    props: true
                },
                {
                    path: '/contacts/:page?',
                    component: Contacts,
                    props: true
                },
                {
                    path: '/request/:id',
                    component: Request,
                    props: true
                }, 
                {
                    path: '/towork/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            status: 'towork',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/replied/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            status: 'replied',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/worked/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            status: 'worked',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/user/assigned/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            user: LS.get('user').id,
                            status: 'assigned',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/user/replied/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            user: LS.get('user').id,
                            status: 'replied',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/user/worked/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            user: LS.get('user').id,
                            status: 'worked',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/category/:category/:page?',
                    component: Inbox,
                    props: true
                }, {
                    path: '/type/:type/:page?',
                    component: Inbox,
                    props: route => {
                        const props = {
                            type: route.params.type,
                            page: route.params.page
                        }
                        if (route.params.type == 2 || route.params.type == 4) {
                            props.status = 4
                        }
                        return props
                    }
                }, {
                    path: '/provider/:provider/:page?',
                    component: Inbox,
                    props: true
                }, {
                    path: 'inbox/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            status: 'inbox',
                            page: route.params.page
                        }
                    }
                }
            ]
        },
        { path: "*", component: NotFound }
    ],
    watch: {
        $route(to, from) {
            console.log(to)
        }
    }
})

window.popStateDetected = false
window.addEventListener('popstate', () => {
    window.popStateDetected = true
})

router.beforeEach((to, from, next) => {
    const authRequired = matchProtectedRoute(to.path)
    const loggedIn = LS.get('user');
    if (authRequired && !loggedIn) {
        return next('/login')
    }
    if (to.path === '/') return next('/inbox')
    next();
})

const publicRoutes = [
    /\/login/i,
    /\/ticket\/\w+/i
]

const matchProtectedRoute = path => {
    for (let i in publicRoutes) {
        let pr = publicRoutes[i]
        if (pr.test(path)) {
            return false
        }
    }
    return true
}

export default router