<template>
    <header class="no-print">
        <div class="row no-gutters">
            <div class="logo text-left pl-3" style="width: 47px">
                <!--
                <div @click="$emit('menu')">
                    <i class="fas fa-bars"></i>
                </div>
                -->
            </div>
            <router-link to="/inbox" class="h4 ml-4 text-white mb-0" style="line-height:60px; width: 235px">
                <strong><span>Mercatorumsa</span><span class="font-weight-bolder crm">CRM</span></strong>
            </router-link>
            <div class="col">
                <nav class="px-4">
                    <div class="row align-items-center">
                        <div class="col-6 position-relative" v-click-outside="closeSearchResults">
                            <input type="text" class="form-control rounded-0" placeholder="Cerca contatto" v-model="search" @focus="searchContact" @blur="focused = false" :readonly="!focused">
                            <div class="position-absolute w-100 search-results">
                                <div class="bg-white search-results-list">
                                    <div v-if="loading" class="text-secondary text-center"><i class="fas fa-circle-notch fa-spin"></i></div>
                                    <router-link id="div" class="px-2 bg-white text-secondary row no-gutters" v-for="(contact, index) in this.search_results" :to="`/contact/${contact.id}`" :key="index">
                                        <div class="col-6 text-left">
                                            {{ contact.first_name }} {{ contact.last_name }}
                                        </div>
                                        <div class="col-3 text-left">{{ contact.email }}</div>
                                        <div class="col-3 text-right">{{ contact.fiscal_code }}</div>
                                    </router-link>
                                </div>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="d-flex justify-content-end">
                                <div class="mr-4">
                                    <a href="https://www.unimercatorum.it" target="_blank">
                                        <i class="far fa-arrow-alt-circle-right mr-2"></i>www.unimercatorum.it
                                    </a>
                                </div>
                                <div class="dropdown" v-click-outside="closeDropDown">
                                    <a @click="open = !open" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                        {{ user.first_name }} {{ user.last_name}}
                                        <i class="fas fa-caret-down ml-2"></i>
                                    </a>
                                    <div class="dropdown-menu rounded-0" :class="{show: open}" aria-labelledby="navbarDropdown">
                                        <div class="dropdown-item color-secondary cursor-pointer" href="https://crm.unipegaso.app/logout" @click="logout"><i class="fas fa-sign-out-alt mr-2"></i>Esci</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
        </div>
    </header>
</template>
<script>
import services from '../../services'
import { mapState } from 'vuex'

export default {
    name: 'main-header',
    props: {
        menu: {
            type: Boolean,
            required: true
        }
    },
    data () {
        return {
            focused: false,
            open: false,
            search: '',
            search_results: [],
            loading: false
        }
    },
    methods: {
        closeDropDown () {
            this.open = false
        },
        closeSearchResults () {
            this.search_results = []
        },
        logout () {
            services.signout()
        },
        searchContact () {
            this.focused = true
            if (this.search.trim() === '') return
            clearTimeout(this.$options.searchTimeout)
            this.$options.searchTimeout = setTimeout(() => {
                this.loading = true
                this.search_results = []
                services.searchContact(this.search.trim()).then(results => {
                    this.loading = false
                    this.search_results = results
                })
            }, 500)
        }
    },
    computed: {
        ...mapState('auth', {
            user: 'user'
        })
    },
    searchTimeout: null,
    watch: {
        search () {
            this.searchContact()
        }
    }
}
</script>
<style scoped>
    .search-results {
        padding-left: inherit;
        padding-right: inherit;
        position: absolute;
        left: 0;
        right: 0;
    }
    .search-results .search-results-list {
        
        -webkit-box-shadow: 0px 5px 8px -4px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 5px 8px -4px rgba(0,0,0,0.75);
        box-shadow: 0px 5px 8px -4px rgba(0,0,0,0.75);
    }
    .logo {
        height: 60px;
        line-height: 60px;
        cursor: pointer;
        color: #fff;
        background-color: #a12c2f;
    }
    * {
       -webkit-touch-callout: none;
       -webkit-user-select: none;
       -khtml-user-select: none;
       -moz-user-select: none;
       -ms-user-select: none;
       user-select: none;
    }
    
    header {
        position: relative;
        max-height: 100px;
        z-index: 1030;
        background-color: #a12c2f;
        -webkit-app-region: drag;
    }

    header nav {
        margin:13px 0;
    }

    header a {
        color:#fff;
        font-weight: bold;
    }

    a#navbarDropdown{
        cursor: pointer;
        color:#fff;
    }

</style>