<template>
    <router-link tag="li" :to="`/request/${id}`" :class="liClass">
        <div class="row align-items-center">
            <div class="col-2">
                <div class="d-block lh-100">
                    <span class="d-block">
                        {{ `${contact.first_name} ${contact.last_name}` }}
                        {{ replyCount }}
                    </span>
                </div>
            </div>
            <div class="col-5 text-truncate">
                <span class="btn btn-xs mr-1 rounded-0" :class="type.class">{{ type.name }}</span>
                <span class="btn btn-xs btn-outline-secondary mr-1 rounded-0">{{ category.name }}</span>
                <span class="text-muted">{{ subject }}</span>
            </div>
            <div class="col-1"> 
                <span class="small text-truncated" v-html="formatProvider"></span>
            </div>
            <div class="col-3 text-left">
                <div class="d-flex align-items-center">
                    <div class="w-50 pr-2">
                        <span v-if="user.id"><i class="far fa-user-circle mr-2"></i>{{ user.first_name }} {{ user.last_name }}</span>
                    </div>
                    <div class="w-50">
                        <button @click.stop="assign" :class="`btn btn-sm ${status.class} btn-block rounded-0`" :disabled="loading">
                            <i v-if="loading" class="fas fa-circle-notch fa-spin"></i>
                            <i v-else :class="`${status.icon} mr-2`"></i>
                            {{ status.name }}
                        </button>
                    </div>
                </div>
            </div>
            <div class="col-1 text-right text-muted small"><date-time :at="lastDate" /></div>
        </div>
    </router-link>
</template>
<script>
    import services from '../../services'

    export default {
        name: 'request-item',
        props: {
            id: {
                type: Number,
                required: false
            },
            user: {
                type: Object,
                required: false
            },
            contact: {
                type: Object,
                required: true
            },
            type: {
                type: Object,
                required: true
            },
            category: {
                type: Object,
                required: true
            },
            status: {
                type: Object,
                required: true
            },
            created_at: {
                type: String,
                required: true
            },
            subject: {
                type: String,
                required: true
            },
            provider: {
                type: String,
                required: false,
                default: 'Sito Web'
            },
            reply: {
                type: Object,
                required: true
            },
            disabled: {
                type: Boolean,
                default: false,
                required: false
            }
        },
        data () {
            return {
                loading: false,
                updated: false
            }
        },
        computed: {
            liClass () {
                return {
                    'shake animated': this.updated,
                    'rounded-0': true,
                    'list-group-item': true,
                    'pointer': true,
                    'bg-light': true,
                    'cursor-pointer': true,
                    disabled: this.disabled
                }
            },
            replyCount() {
                return (this.reply.count !== null) ? '(' + (this.reply.count + 1) + ')' : '';
            },
            lastDate() {
                return (this.reply.last_date !== null) ? this.reply.last_date : this.created_at;
            },
            formatProvider() {
                switch (this.provider) {
                    case 'website':
                        return '<i class="fas fa-desktop mr-1"></i>Sito Web';
                        break;
                    default:
                        return $this.provider;
                }
            }
        },
        methods: {
            assign () {
                if (this.status.id === 1 && !this.user.id) {
                    this.loading = true
                    services.assignRequest(this.id).then(data => {
                        if (data === true) {
                            this.$router.push(`/request/${this.id}`)
                        }
                    }).catch(error => {
                        alert(error)
                    }).then(() => {
                        this.loading = false
                    })
                } else {
                    this.$router.push(`/request/${this.id}`)
                }
            }
        },
        watch: {
            status (o, n) {
                if (o.id != n.id) {
                    this.updated = true
                    setTimeout(() => {
                        this.updated = false
                    }, 1000)
                }
                
            }
        }
    }
</script>
<style scoped>
    li.disabled {
        opacity: .5;
        transition: all .3s ease;
    }
    .list-group .btn-xs, table .btn-xs {
        padding: 0 .5rem;
        font-size: .875rem;
        line-height: 1.5;
        border-radius: .2rem;
    }
    .list-group-item {
        cursor: pointer;
    }
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
</style>