<template>
    <li class="rounded-0 list-group-item bg-light" @click="$emit('click', call.note)">
        <div class="row align-items-center">
            <div class="col-2">
                <router-link :to="`/contact/${call.contact_id}`">{{ `${call.contact_first_name} ${call.contact_last_name}` }}</router-link>
            </div>
            <div class="col-5 text-truncate">
                <span class="btn btn-xs mr-1 rounded-0 text-white bg-success">{{ directionName }}</span>
                <span class="btn btn-xs btn-outline-secondary mr-1 rounded-0">{{ call.category_name }}</span>
                <span class="text-muted">{{ call.subject }}</span>
            </div>
            <div class="col-1 small text-truncate">
                <!-- note -->
                <button data-toggle="modal" class="btn btn-xs btn-block btn-outline-secondary mr-1 rounded-0" @click="$emit('note', call.note)"><i class="far fa-file-alt mr-2"></i>Note</button>
            </div>
            <div class="col-3 text-left">
                <div class="d-flex align-items-center">
                    <div class="w-50 pr-2">
                        <i class="far fa-user-circle mr-2"></i>{{ userName }}
                    </div>
                    <div class="w-50">
                        <span class="btn btn-sm btn-block mr-1 rounded-0 bg-info text-white">{{ responseName }}</span>
                    </div>
                </div>
            </div>
            <div class="col-1 text-right text-muted small"><date-time :at="call.created_at" /></div>
        </div>
    </li>
</template>
<script>
    
    export default {
        name: 'call-item',
        props: ['call'],
        directions: {
            '1': 'In Entrata',
            '2': 'In Uscita'
        },
        responses: [
            'Nessuna Risposta',
            'Non Interessato',
            'Poco Interessato',
            'Interessato',
            'Molto Interessato',
            'Da Ignorare'
        ],
        computed: {
            responseName () {
                return this.$options.responses[this.call.response]
            },
            directionName () {
                return this.$options.directions[this.call.direction]
            },
            contactName () {
                return this.call.contact_first_name + ' ' + this.call.contact_last_name
            },
            userName () {
                return this.call.user_first_name.substring(0, 1) + '. ' + this.call.user_last_name
            }
        }
    }
</script>
<style scoped>
    li.disabled {
        opacity: .5;
        transition: all .3s ease;
    }
    .list-group .btn-xs, table .btn-xs {
        padding: 0 .5rem;
        font-size: .875rem;
        line-height: 1.5;
        border-radius: .2rem;
    }
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
</style>