<template>
    <div :class="classes">
        <div class="chat-header d-flex justify-content-between p-2 text-white" @click="$emit('toggle', user.id)">
            <div class="chat-title d-flex align-items-center">
                <small><i :class="`online fas fa-circle font-weight-bold mr-2 small ${user.online ? 'text-success' : 'text-secondary'}`"></i></small>
                {{ user.first_name }} {{ user.last_name }}
                <span class="ml-2 new badge badge-light" v-if="newMessages">Nuovo</span>
            </div>
            <div class="chat-buttons d-flex px-2">
                <div class="chat-minimize mr-3"><i :class="iconClass"></i></div>
                <div class="chat-close"><i class="fas fa-times" @click.stop="$emit('close')"></i></div>
            </div>
        </div>
        <div class="chat-body">
            <div class="message-box" ref="box">
                <div class="text-center text-muted cursor-pointer" @click="moreHistory" v-if="more_history">carica messaggi precedenti</div>
                <div class="history-box"></div>
                <chat-message v-for="(message, index) in messages" :message="message" :key="index" />
            </div>
            <div class="text-area p">
                <div class="text-muted small font-italic typing p-2 d-block"></div>
                <textarea @focus="focus" ref="textarea" placeholder="Scrivi qui il messaggio" class="chat-input p-3" v-model="input" @keydown="keydown"></textarea>
            </div>
        </div>
    </div>
</template>
<script>
    import { mapState } from 'vuex'
    import services from '../../services'
    import LS from '../../services/localstorage'
    import ChatMessage from './ChatMessage'
    import store from '../../store'

    export default {
        name: 'conversation',
        components: { ChatMessage },
        data () {
            return {
                new: false,
                input: ''
            }
        },
        props: {
            user: {
                type: Object,
                required: true
            },
            messages: {
                type: Array,
                required: false,
                default: () => []
            },
            more_history: {
                type: Boolean,
                required: false,
                default: true
            },
            minimized: {
                type: Boolean,
                required: false,
                default: false
            }
        },
        methods: {
            focus () {
                store.commit('chat/unset_unread_messages_count', this.user.id)
                services.ws.send('READ_MESSAGES', this.user.id)
            },
            keydown (e) {
                this.focus()
                if (e.which === 13) {
                    e.preventDefault()
                    this.send()
                    return false
                }
            },
            toggle () {
                this.minimized = !this.minimized
            },
            send () {
                const text = this.input.trim()
                services.ws.send('CHAT_MESSAGE', {
                    to: this.user.id,
                    text
                })
                this.input = ''
            },
            loadHistory (last_id = null) {
                const payload = {
                    from: this.user.id
                }
                if (last_id) payload.last_id = last_id
                services.ws.send('HISTORY', payload)
            },
            moreHistory () {
                this.loadHistory(this.messages[0].id)
            }
        },
        computed: {
            newMessages () {
                return !!this.messages.find(m => !m.read_at && m.to === LS.get('user').id)
            },
            classes () {
                return {
                    minimized: this.minimized,
                    chat: true,
                    'chat mr-2': true
                }
            },
            iconClass () {
                return {
                    'far fa-window-maximize': this.minimized,
                    'fas fa-minus': !this.minimized
                }
            }
        },
        watch: {
            messages: {
                handler (n, o) {
                    if (n.length < 1) return
                    if (o.length < 1) {
                        this.$nextTick().then(() => {
                            this.$refs.box.scrollTop = this.$refs.box.scrollHeight
                        })
                        return
                    }
                    if (o[o.length - 1].id < n[n.length - 1].id) {
                        this.$nextTick().then(() => {
                            this.$refs.box.scrollTop = this.$refs.box.scrollHeight
                        })
                    }
                }
            },
            minimized (n, o) {
                if (!n) {
                    this.$nextTick().then(() => {
                        this.$refs.textarea.focus()
                    })
                }
            }
        },
        mounted () {
            this.$refs.textarea.focus()
            this.$refs.box.scrollTop = this.$refs.box.scrollHeight
            this.loadHistory()
        }
    }
</script>
<style scoped>
    .chat-title {
    }

    .chat .chat-body {

    }

    .chat.minimized .chat-body {
        height: 0;
        transition: all 400ms cubic-bezier(0.500, 0.250, 0.090, 1);;
    }

    .chat .chat-body {
        height: 396px;
        transition: all 400ms cubic-bezier(0.500, 0.250, 0.090, 1);;
    }

    .message-box {
        height: 300px;
        overflow: auto;
    }
    .chat {
        width: 300px;
        -webkit-box-shadow: 3px -3px 7px -3px rgba(0,0,0,0.55);
        -moz-box-shadow: 3px -3px 7px -3px rgba(0,0,0,0.55);
        box-shadow: 3px -3px 7px -3px rgba(0,0,0,0.55);
    }
    .chat .message-box {
        background: white;
    }

    
    .chat textarea {
        resize: none;
        display:block;
        border: none;
        overflow: auto;
        outline: none;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        border-top: 1px solid #ccc;
        width:100%;
        margin:0;
    }

    .chat {
        background:white;
    }

    .chat-header {
        background-color: #5c112d;
        border-color: #5c112d;
        cursor: pointer;
    }

    .chat-header.blink {
        animation:blink 2s;
        -moz-animation:blink 2s infinite; /* Firefox */
        -webkit-animation:blink 2s infinite;
    }
    .message-text-div {
        word-break: break-word;
    }

    @-moz-keyframes blink
    {
        0%   {background:#5c112d; border-color: #5c112d;}
        50%   {background:#802144; border-color:#802144;}
        100%   {background:#5c112d; border-color: #5c112d;}
    }
    
    @-webkit-keyframes blink
    {
        0%   {background:#5c112d; border-color: #5c112d;}
        50%   {background:#802144; border-color:#802144;}
        100%   {background:#5c112d; border-color: #5c112d;}
    }
</style>