import Vue from 'vue'

const state = {
    messages: [],
    unread_messages: [],
    users: [],
    online: false
}

const mutations = {
    set_messages_as_read (state, payload) {
        for (let i = 0; i < state.messages.length; i++) {
            if (state.messages[i].from.id == payload.id) {
                Vue.set(state.messages, i, {
                    ...state.messages[i],
                    read_at: payload.read_at
                })
            }
        }
    },
    unset_unread_messages_count (state, id) {
        Vue.set(state.unread_messages, id, 0)
    },
    set_unread_messages_count (state, messages) {
        for (let i in messages) {
            let id = messages[i].from_id
            if (state.unread_messages[id] === undefined) {
                Vue.set(state.unread_messages, id, 0)
            }
            Vue.set(state.unread_messages, id, state.unread_messages[id] + 1)
        }
    },
    history (state, messages) {
        messages = messages.map(message => {
            return {
                id: message.id,
                from: {
                    first_name: message.from_first_name,
                    last_name: message.from_last_name,
                    id: message.from_id,
                    photo: message.from_photo
                },
                to: message.to_id,
                text: message.text,
                read_at: message.read_at,
                created_at: message.created_at
            }
        })
        for (let i = 0; i < messages.length; i++) {
            if (state.messages.findIndex(m => m.id === messages[i].id) > -1) continue
            state.messages.unshift(messages[i])
        }
    },
    set_users (state, users) {
        state.users = users
    },
    set_online_user (state, user_id) {
        const index = state.users.findIndex(u => u.id == user_id)
        if (index > -1) state.users[index].online = true
    },
    set_offline_user (state, user_id) {
        const index = state.users.findIndex(u => u.id == user_id)
        if (index > -1) state.users[index].online = false
    },
    add_message (state, message) {
        state.messages.push(message)
    },
    set (state, posts) {
        state.items = posts
    },
    current (state, post) {
        state.current = post
    }
}
const actions = {
    send ({ commit }, data) {
        
    },
    addMessage ({ commit }, data) {

    },
    addChat ({ commit }, data) {
         
    }
}

const getters = {
    all: state => state.items,
    allUsers: state => state.users,
    onlineUsers: state => state.users.filter(u => u.online),
    offlineUsers: state => state.users.filter(u => !u.online),
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}