<template>
    <page classes="p-4">
        <div class="row mb-3"> 
            <div class="col-6">
                <div class="btn-group"> 
                    <button @click="$router.go(-1)" class="btn btn-outline-secondary btn-sm rounded-0 px-3"><i class="fas fa-chevron-left mr-2"></i>TORNA INDIETRO</button>
                    <button @click="closed = !closed" class="btn btn-outline-secondary btn-sm rounded-0 px-3"><i :class="`fas ${closed ? 'fa-plus' : 'fa-minus'} mr-2`"></i>CREA UTENTE</button>
                </div>
            </div>
        </div>
        <div :class="classes">
            <div class="card-header bg-white">
                <h5 class="m-0">Crea Utente</h5>
            </div>
            <div class="card-body">
                 
                <div class="form-group row">
                    <label for="first_name" class="col-sm-2 col-form-label text-right">Nome</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control rounded-0" name="first_name" placeholder="Nome" id="first_name" required="" v-model="first_name">
                    </div>
                </div>
                
                <div class="form-group row">
                    <label for="last_name" class="col-sm-2 col-form-label text-right">Cognome</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control rounded-0" name="last_name" placeholder="Cognome" id="last_name" required="" v-model="last_name">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="email" class="col-sm-2 col-form-label text-right">E-Mail</label>
                    <div class="col-sm-10">
                        <input type="email" class="form-control rounded-0" name="email" placeholder="E-Mail" id="email" required="" v-model="email">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="role" class="col-sm-2 col-form-label text-right">Ruolo</label>
                    <div class="col-sm-10">
                        <select class="custom-select rounded-0" id="role" name="role" required="" v-model="role">
                            <option value="1">Operatore</option>
                            <option value="2">Amministratore</option>
                        </select>
                    </div>
                </div>
                    
                <div class="form-group row">
                    <label for="password" class="col-sm-2 col-form-label text-right">Password</label>
                    <div class="col-sm-10">
                        <input type="password" class="form-control rounded-0" name="password" placeholder="Password" id="password" required="" v-model="password">
                    </div>
                </div> 

                <div class="form-group row">
                    <label for="repeat_password" class="col-sm-2 col-form-label text-right">Ripeti Password</label>
                    <div class="col-sm-10">
                        <input type="password" class="form-control rounded-0" name="repeat_password" placeholder="Ripeti Password" id="repeat_password" required="" v-model="repeat_password">
                    </div>
                </div>
        
                <div class="form-group row">
                    <label for="photo" class="col-sm-2 col-form-label text-right">Foto</label>
                    <div class="col-sm-10">
                        <input type="file" class="form-control rounded-0" name="photo" placeholder="Photo" id="photo" accept="image/*" required="" @change="fileChange">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="photo" class="col-sm-2 col-form-label text-right"></label>
                    <div class="col-sm-10">
                        <ui-button class="btn btn-primary rounded-0" @click="send" :loading="loading" icon="fas fa-plus">Crea</ui-button>
                    </div>
                </div>

            </div>
        </div>
        <ul class="list-group users my-4">
            <li class="rounded-0 list-group-item" v-for="(user, index) in users" :key="index">
                <div class="row align-items-center">
                    <div class="col-4 font-weight-bold">
                        <div class="d-flex align-items-center"> 
                            <div class="mr-3"><img :src="`https://crm.unimercatorum.it/uploads/users/${user.photo}`" height="50"></div>
                            <div class="h5 m-0">{{ user.first_name }}</div>
                        </div>
                    </div>
                    <div class="col-5">{{ user.email }}</div>
                    <div class="col-3">{{ roleName(user.role) }}</div>
                </div>
            </li>
        </ul>
    </page>
</template>
<script>
    import services from '../services'
    export default {
        name: 'users',
        data () {
            return {
                first_name: '',
                last_name: '',
                email: '',
                role: 1,
                password: '',
                repeat_password: '',
                photo: null,
                closed: true,
                users: [],
                loading: false
            }
        },
        roles: [
            'Operatore',
            'Amministratore'
        ],
        methods: {
            fileChange (e) {
                this.photo = e.target.files[0]
            },
            send () {
                this.loading = true
                services.createUser({
                    first_name: this.first_name,
                    last_name: this.last_name,
                    email: this.email,
                    role: this.role,
                    password: this.password,
                    repeat_password: this.repeat_password,
                    photo: this.photo
                }).catch(error => {
                    alert(error)
                }).then(data => {
                    this.loading = false
                    console.log(data)
                })
            },
            load () {
                services.getUsers().then(data => {
                    this.users = data
                })
            },
            roleName (role) {
                return this.$options.roles[role]
            }
        },
        mounted () {
            this.load()
        },
        computed: {
            classes () {
                return {
                    'card': true,
                    'rounded-0': true,
                    'd-none': this.closed
                }
            }
        }
    }
</script>