<template>
    <page classes="p-4">
        <div v-if="!contact" class="text-center py-4">
            <span style="font-size:25px"><i class="fas fa-circle-notch fa-spin"></i></span>
        </div>
        <div v-else>
            <div class="row mb-3">
                <div class="col">
                    <h1>{{ name }}</h1>
                </div>
            </div>
            <toolbar />
            <div class="card rounded-0 mb-3">
                <div class="card-header bg-white">
                    <h5 class="m-0">
                        Contatto
                        <span class="float-right small"></span>
                    </h5>
                </div>
                <div class="card-body">
                    <div class="row pb-3 mb-3 border-bottom">
                        <div class="col">
                            <div class="d-block"><i class="far fa-user-circle mr-2"></i>Nome e Cognome</div>
                            <div class="ml-4 font-weight-bold">{{ name }}</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="far fas fa-graduation-cap mr-2"></i>Studente</div>
                            <div class="ml-4 font-weight-bold">No</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="fas fa-qrcode mr-2"></i>Matricola</div>
                            <div class="ml-4 font-weight-bold">-</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="far fa-user-circle mr-2"></i>Iscritto il</div>
                            <div class="ml-4 font-weight-bold">-</div>
                        </div>
                    </div>
                    <div class="row pb-3 mb-3 border-bottom">
                        <div class="col">
                            <div class="d-block"><i class="far fa-credit-card mr-2"></i>Codice Fiscale</div>
                            <div class="ml-4 font-weight-bold">{{ contact.fiscal_code }}</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="far fa-envelope mr-2"></i>E-Mail</div>
                            <div class="ml-4 font-weight-bold">{{ contact.email }}</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="fa fa-phone mr-2"></i>Telefono</div>
                            <div class="ml-4 font-weight-bold">{{ contact.telephone }}</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="fa fa-transgender mr-2"></i>Sesso</div>
                            <div class="ml-4 font-weight-bold">{{ contact.gender }}</div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col">
                            <div class="d-block"><i class="fas fa-birthday-cake mr-2"></i>Data di Nascita</div>
                            <div class="ml-4 font-weight-bold">-</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="fas fa-map-marker mr-2"></i>Città</div>
                            <div class="ml-4 font-weight-bold">-</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="fas fa-map-marker mr-2"></i>Provincia</div>
                            <div class="ml-4 font-weight-bold">-</div>
                        </div>
                        <div class="col">
                            <div class="d-block"><i class="fas fa-map-marker mr-2"></i>CAP</div>
                            <div class="ml-4 font-weight-bold">-</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="card rounded-0 mb-3">
                <div class="card-header bg-white">
                    <h5 class="m-0">Richieste</h5>
                </div>
                <div class="card-body">
                    <ul class="list-group">
                        <request-item v-if="requests.length > 0" v-for="(request, index) in requests"
                            :key="index"
                            :id="request.id"
                            :contact="{
                                id: request.cid,
                                first_name: request.first_name,
                                last_name: request.last_name
                            }"
                            :user="{
                                id: request.user_id,
                                first_name: request.user_first_name,
                                last_name: request.user_last_name    
                            }"
                            :first_name="request.first_name"
                            :last_name="request.last_name"
                            :type="{
                                id: request.type,
                                class: request.type_class,
                                icon_color: request.type_color_icon,
                                name: request.type_name
                            }"
                            :subject="request.subject"
                            :status="{
                                id: request.status,
                                class: request.status_class,
                                icon: request.status_icon,
                                name: request.status_name
                            }"
                            :category="{
                                id: request.category,
                                name: request.category_name
                            }"
                            :reply="{
                                count: request.reply_count,
                                last_date: request.reply_last_date
                            }"
                            :created_at="request.created_at"
                            :provider="request.provider"
                        />
                    </ul>
                </div>
            </div>
            <div class="card rounded-0 mb-3" v-if="calls.length">
                <div class="card-header bg-white">
                    <h5 class="m-0">Telefonate</h5>
                </div>
                <div class="card-body">
                    <ul class="list-group">
                        <li v-if="loading" key="loading" class="rounded-0 list-group-item pointer bg-light ml-auto text-center">
                            <span v-if="loading">
                                <i class="fas fa-circle-notch fa-spin"></i>
                            </span>
                        </li>
                        <call-item
                            v-else
                            @note="note => modal = note"
                            v-for="(call, index) in calls"
                            :key="index"
                            :call="call"
                        />
                    </ul>
                </div>
                <modal :open="modal ? true : false" @close="modal = false" title="Note Telefonata">
                    <template v-slot:footer>
                        <button data-v-3dea25d8="" type="button" data-dismiss="modal" class="btn btn-secondary" @click="modal = false">Chiudi</button>
                    </template>
                    {{ modal }}
                </modal>
            </div>
        </div>
    </page>
</template>
<script>
    import Toolbar from './Toolbar'
    import services from '../services'
    import RequestItem from './inbox/RequestItem'
    import CallItem from './call/CallItem'
    import Modal from './layout/Modal'

    export default {
        name: 'contact',
        components: { Toolbar, RequestItem, CallItem, Modal },
        props: {
            id: {
                type: String,
                required: true
            }
        },
        data () {
            return {
                modal: false,
                loading: false,
                contact: false,
                requests: [],
                calls: []
            }
        },
        mounted () {
            this.load()  
        },
        methods: {
            load () {
                this.loading = true
                services.getContact(this.id).then(data => {
                    this.contact = data
                    this.requests = data.requests
                    this.calls = data.calls
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading = false
                })
            }
        },
        computed: {
            name () {
                return `${ this.contact.first_name } ${ this.contact.last_name}`
            }
        }
    }
</script>