<template>
    <page classes="p-4 d-flex flex-column">
        <toolbar />
        <analytics-widgets v-if="inInbox" />
        <div class="row my-3">
            <div class="col-6">
                <button type="button" class="btn btn-outline-secondary btn-sm rounded-0 px-3" :class="{disabled: loading}" @click="refresh"><i :class="{'fa-spin': loading}" class="fas fa-sync-alt mr-2 pause-spinner"></i>AGGIORNA</button>
            </div>
            <div v-if="requests.length > 0" class="col-6 text-right">
                <div class="btn-group pull-right">
                    <div class="mt-1 mr-2">
                        <span v-if="loading"><i class="fas fa-circle-notch fa-spin"></i></span>
                        <span v-else><span class="range-start">{{ start }}</span>-<span class="range-end">{{ end }}</span></span>
                        di <span class="range-total">{{ total }}</span>
                    </div>
                    <router-link tag="button" :disabled="prevDisabled" :to="prevPageLink" class="btn btn-outline-secondary btn-sm px-3 rounded-0 prev"><i class="fa fa-chevron-left"></i></router-link>
                    <router-link tag="button" :disabled="nextDisabled" :to="nextPageLink" class="btn btn-outline-secondary btn-sm px-3 rounded-0 next"><i class="fa fa-chevron-right"></i></router-link>
                </div>
            </div>
        </div>
        <div style="flex-grow: 1">
            <div class="position-relative slide-wrapper h-100 w-100" style="overflow-x: hidden">
                <horizontal-slide :dir="direction">
                    <ul class="list-group position-absolute h-100 w-100" :key="page">
                        <li v-if="loading" key="loading" class="rounded-0 list-group-item pointer bg-light ml-auto text-center">
                            <span v-if="loading">
                                <i class="fas fa-circle-notch fa-spin"></i>
                            </span>
                        </li>
                        <li v-else-if="requests.length < 1" key="no-results" class="rounded-0 list-group-item pointer bg-light ml-auto text-center">
                            <div>Nessuna richiesta presente</div>
                        </li>
                        <request-item v-for="(request, index) in requests" v-else-if="requests.length > 0" 
                            :disabled="request.busy"
                            :key="index"
                            :id="request.id"
                            :contact="{
                                id: request.cid,
                                first_name: request.first_name,
                                last_name: request.last_name
                            }"
                            :user="{
                                id: request.user_id,
                                first_name: request.user_first_name,
                                last_name: request.user_last_name    
                            }"
                            :first_name="request.first_name"
                            :last_name="request.last_name"
                            :type="{
                                id: request.type,
                                class: request.type_class,
                                icon_color: request.type_color_icon,
                                name: request.type_name
                            }"
                            :subject="request.subject"
                            :status="{
                                id: request.status,
                                class: request.status_class,
                                icon: request.status_icon,
                                name: request.status_name
                            }"
                            :category="{
                                id: request.category,
                                name: request.category_name
                            }"
                            :reply="{
                                count: request.reply_count,
                                last_date: request.reply_last_date
                            }"
                            :created_at="request.created_at"
                            :provider="request.provider"
                        ></request-item>
                    </ul>
                </horizontal-slide>
            </div>
        </div>
    </page>
</template>

<script>
    import Page from './layout/Page';
    import RequestItem from './inbox/RequestItem'
    import services from '../services'
    import Toolbar from './Toolbar'
    import HorizontalSlide from './transitions/HorizontalSlide'
    import AnalyticsWidgets from './analytics/AnalyticsWidgets'
    import { mapGetters, mapActions, mapState } from 'vuex'
    
    export default {
        name: 'inbox',
        components: {Page, RequestItem, AnalyticsWidgets, HorizontalSlide, Toolbar},
        props: {
            type: {
                type: Number | Boolean,
                required: false,
                default: false
            },
            status: {
                type: Number | Boolean,
                required: false,
                default: false
            },
            category: {
                type: Number | Boolean,
                required: false,
                default: false
            },
            user: {
                type: Number | Boolean,
                required: false,
                default: false
            },  
            provider: {
                type: String,
                required: false,
                default: 'website'
            },
            page: {
                type: Number | String,
                required: false,
                default: 1
            }
        },
        data () {
            return {
                //start: 0,
                //end: 0,
                //quantity: 0,
                //current_page: 1,
                //pages: 0,
                //total: 0,
                //loading: false,
                //requests: [],
                //loading: false,
                direction: 1,
            }
        },
        computed: {
            prevDisabled () {
                return !this.prev || this.loading
            },
            nextDisabled () {
                return !this.next || this.loading
            },
            prevPageLink () {
                if (!this.prev) return this.$route.path
                return this.basePath + '/' + this.prev
            },
            nextPageLink () {
                if (!this.next) return this.$route.path
                return this.basePath + '/' + this.next
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            },
            next () {
                const next = parseInt(this.page) + 1
                if (next > this.pages) return undefined
                return next
            },
            prev () {
                const prev = parseInt(this.page) - 1
                if (prev === 0) return undefined
                return prev
            },
            params () {
                return {
                    status: this.status,
                    category: this.category,
                    user: this.user,
                    type: this.type,
                    provider: this.provider,
                    page: this.page
                }
            },
            inInbox () {
                return /\/inbox(\/\d+)?/i.test(this.$route.path)
            },
            ...mapState('request', {
                requests: state=> state.requests.items,
                start: state => state.requests.start + 1,
                end: state => state.requests.end,
                current_page: state => state.requests.page,
                pages: state => state.requests.pages,
                total: state => state.requests.total,
                quantity: state=> state.requests.qty,
                loading: 'loading'
            })
        },
        methods: {
            ...mapActions('request', {
                getRequests: 'getAll'
            }),
            refresh () {
                if (this.loading) return
                this.load()
            },
            load () {
                this.getRequests(this.params)
            }
        },
        mounted () {
            this.load()
        },
        watch: {
            params (new_value) {
                this.load()
            },
            page (new_value, prev_value) {
                const diff = prev_value - new_value
                this.direction = diff < 0 ? 1 : 0
                this.load()
            }
        }
        
    }
</script>
<style scoped>
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
    .left-0 {
        left: 0;
    }
    .right-0 {
        right: 0;
    }
</style>