<template>
    <page classes="bg-gray">
        <div class="row h-100 justify-content-center align-items-center">
            <div class="col-3">
                <div class="card shadow-sm">
                    <div class="card-header bg-crm"> 
                        <h2 class="text-center my-2 text-white">
                            Mercatorum<span class="font-weight-bold">CRM</span><br>
                        </h2>
                    </div>
                    <div class="card-body p-lg-5">
                        <p class="text-uppercase text-center font-weight-bold">Inserisci le tue credenziali</p>
                        <div>
                            <input type="hidden" name="remember" value="0">
                            <div class="form-group">
                                <input v-model="email" @keydown="keydown" type="email" class="form-control form-control-lg mb-2 rounded-0" id="email" name="email" placeholder="Username" required="">
                            </div>
                            <div class="form-group">
                                <input v-model="password" @keydown="keydown" type="password" class="form-control form-control-lg mb-2 rounded-0" id="password" name="password" placeholder="Password" required="">
                            </div>
                            <button @click="() => signin({email, password})" type="submit" class="btn btn-lg rounded-0 btn-crm btn-block">
                                <strong v-if="!loading">Accedi</strong>
                                <div v-else="loading">
                                    <div class="spinner-border" role="status">
                                        <span class="sr-only">Loading...</span>
                                    </div>
                                </div>
                            </button>
                        </div>
                        <div v-if="errors.length">
                            Credenziali non valide
                        </div>
                        <p class="text-center mt-5 mb-0"><a href="https://www.unimercatorum.it" class="text-uppercase small"><i class="fa fa-arrow-circle-right mr-2"></i>Vai al sito Unimercatorum</a></p>
                    </div>
                </div>
            </div> 
        </div> 
    </page>
</template>

<script>

    import { mapGetters, mapActions, mapState } from 'vuex'
    import Page from './layout/Page';
    export default {
        name: 'login',
        components: {Page},
        data () {
            return {
                email: '',
                password: '',
                remember: false
            }
        },
        methods: {
            ...mapActions('auth', {
                signin: 'signin'
            }),
            keydown (e) {
                if (e.which === 13) this.signin({
                    email: this.email,
                    password: this.password
                })
            }
        },
        computed: {
            ...mapState('auth', {
                user: 'user',
                errors: 'errors'
            }),
            ...mapState('auth', {
                loading: 'logging'
            })
        }
    }
</script>

<style scoped>
    body, html, .bg-gray {
        background-color:#eaeaea!important;
    }
    .btn-crm {
        background-color: #802144;
        border-color: #802144;
        color: #fff;
    }
    .bg-crm{
        background-color: #802144;
    }
    a{
        color:#802144;
    }
    a:hover{
        color:#802144;
    }
</style>
