
import axios from 'axios'
import LS from './localstorage'
import router from '../router'
import store from '../store'


const FAKE_LOADING = false
const WEBSITE_URI = 'https://www.unimercatorum.it';
const URI = 'https://crm.unimercatorum.it'
const BASE_URI = `${URI}/api`


axios.interceptors.response.use(response => {
    if (response.data.status === 401) {
        return router.push('/login')
    }
    return response
  }, error => {
    return Promise.reject(error)
})

String.prototype.trimChar = function (c) {
    if (c === "]") c = "\\]"
    if (c === "\\") c = "\\\\"
    return this.replace(new RegExp(
      "^[" + c + "]+|[" + c + "]+$", "g"
    ), "")
}

const queryString = o => {
    let str = ''
    for (let i in o) {
        if (o[i] === false || o[i] === null) continue
        str += '&' + i + '=' + o[i]
    }
    return str.substring(1, str.length)
}

const request = (auth = true) => {
    
    let options = {}
    let user = LS.get('user')

    if (auth && user) {
        options = {
            headers: {
                Authorization: 'Bearer ' + user.token
            }
        }
    }
    
    const resolve = ({data}) => {
        return new Promise((res, rej) => {
            if (FAKE_LOADING) {
                setTimeout(() => {
                    if (data.status === 200) return res(data.message)
                    return rej(data.message)
                }, Math.floor(Math.random(1) * 500))
            } else {
                if (data.status === 200) return res(data.message)
                return rej(data.message)
            }
        })
    }

    const reject = error => {
        return Promise.reject(error)
    }
    const get = (url, opt ={}) => {
        url = `${BASE_URI}/${url.trimChar('/')}`
        return axios.get(url, {...options, ...opt}).then(resolve).catch(reject)
    }
    const post = (url, data = {}, opt = {}) => {
        url = `${BASE_URI}/${url.trimChar('/')}`
        return axios.post(url, data, {...options, ...opt}).then(resolve).catch(reject)
    }
    const put = (url, data = {}, opt = {}) => {
        url = `${BASE_URI}/${url.trimChar('/')}`
        return axios.put(url, data, {...options, ...opt}).then(resolve).catch(reject)
    }
    const patch = (url, data = {}, opt = {}) => {
        url = `${BASE_URI}/${url.trimChar('/')}`
        return axios.put(url, data, {...options, ...opt}).then(resolve).catch(reject)
    }
    const _delete = (url, opt = {}) => {
        url = `${BASE_URI}/${url.trimChar('/')}`
        return axios.delete(url, {...options, ...opt}).then(resolve).catch(reject)
    }
    return {
        get,
        post,
        put,
        patch,
        delete: _delete
    }
}

class SocketService extends require('events').EventEmitter
{
    constructor ()
    {
        super()
        this.autoReconnectTimeout = 5 * 1000
    }

    open ()
    {
        this.user = LS.get('user')
        if (!this.user) return
        this.url = 'ws://ws.unimercatorum.it?token=' + this.user.token
        this.socket = new WebSocket(this.url)
        this.init()
    }

    reconnect ()
    {
        console.log(`WebSocketClient: retry in ${this.autoReconnectTimeout} ms`)
        this.socket = null
        setTimeout(() => {
            this.open(this.url)
        }, this.autoReconnectTimeout)
    }

    init ()
    {
        this.socket.onopen = () => this.emit('connection')
        this.socket.onerror = err => this.emit('error', err)
        this.socket.onclose = e => {
            this.reconnect(e)
            this.emit('close')
        }
        this.socket.onmessage = ({ data }) => {
            try {
                const json = JSON.parse(data)
                let event = {
                    type: json.type,
                    payload: json.payload
                }
                this.emit('message', event)
                this.emit(event.type, event.payload)
            } catch (error) {
                this.emit('error', error)
            }
        }
    }

    send(type, payload = null)
    {
        this.socket.send(JSON.stringify({
            type,
            payload
        }))
    }
}

const services = {
    //ws: new SocketService(),
    getDegrees: () => {
        return request().get('course/degree')
    },
    getCalls: params => {
        const queryParams = queryString(params)
        return request().get(`call?${queryParams}`)
    },
    sendCall: (data, progress = () => {}) => {
        return request().post('call', data)
    },
    chatHistory: params => {
        const queryParams = queryString(params)
        return request().get(`chat/history?${queryParams}`)
    },
    searchContact: search => {
        return request().get(`contact/search?search=${search}`)
    },
    getUser: id => {
        return request().get('user')
    },
    getUsers: () => {
        return request().get('user')
    },
    updateUser: (id, data, progress = () => {}) => {
        const fd = new FormData()
        for (let key in data) {
            fd.append(key, data[key])
        }
        return request().put(`user/${id}`, fd, {
            onUploadProgress: progress
        })
    },
    createUser: (data, progress = () => {}) => {
        const fd = new FormData()
        for (let key in data) {
            fd.append(key, data[key])
        }
        return request().post('user', fd, {
            onUploadProgress: progress
        })
    },
    getAnalyticsWidgets: () => {
        return request().get('analytics/widgets')
    },
    getContacts: params => {
        const queryParams = queryString(params)
        return request().get(`contact?${queryParams}`)
    },
    getContact: id => {
        return request().get(`contact/${id}`)
    },
    sendAnswer: (data, progress = () => {}) => {
        const fd = new FormData()
        data.attachments.forEach((attachment, i) => {
            fd.append(`attachments[${i}]`, attachment)
        })
        fd.append('rid', data.rid)
        fd.append('cuid', data.cuid)
        fd.append('body', data.content)
        return request().post('/reply', fd, {
            onUploadProgress: progress
        })
    },
    sendPublicAnswer: (token, data, progress = () => {}) => {
        const fd = new FormData()
        data.attachments.forEach((attachment, i) => {
            fd.append(`attachments[${i}]`, attachment)
        })
        fd.append('body', data.content)
        return request().post(`reply/${token}`, fd, {
            onUploadProgress: progress
        })
    },
    setRequestAsTrash: id => {
        return request().get(`/request/${id}/trash`)
    },
    setRequestAsWorked: id => {
        return request().get(`request/${id}/worked`)
    },
    setRequestAsToWork: id => {
        return request().get(`request/${id}/work`)
    },
    assignRequest: id => {
        return request().get(`request/${id}/assign`)
    },
    getPublicRequest: token => {
        return request().get(`reply/${token}`)
    },
    getRequest: id => {
        return request().get(`request/${id}`)
    },
    getRequests: params => {
        const queryParams = queryString(params)
        return request().get(`request?${queryParams}`)
    },
    getUser: () => {
        return request().get('auth')
    },
    signin: (email, password) => {
        return new Promise((resolve, reject) => {
            axios.post(`${BASE_URI}/auth`, {
                email,
                password
            }).then(({ data }) => {
                if (data.status === 200) {
                    LS.set('user', data.message)
                    //services.ws.open()
                    return resolve(data.message)
                }
                reject(data.message)
            }).catch(error => {
                reject()
            })
        })
    },
    signout: () => {
        LS.remove('user')
        router.push('/login')
    },
    requests: () => {
        return request.get('requests')
    },
    uri: URI,
    api_uri: BASE_URI,
    website_uri: WEBSITE_URI,
    userPhoto: file => `${URI}/uploads/users/${file}`
}
