<template>
    <div :class="classes" v-if="own">
        <div class="mr-2 chat-photo-container">
            <img :src="`/uploads/users/${message.from.photo}`" class="chat-photo">
        </div>
        <div class="w-100 message-text">
            <div class="message-text-div">{{ message.text }}</div>
            <div>
                <div class="small text-muted message-date"><date-time :at="message.created_at" /></div>
            </div>
        </div>
    </div>
    <div :class="classes" v-else>
        <div class="w-100 message-text">
            <div class="message-text-div text-right">{{ message.text }}</div>
            <div>
                <div class="small text-muted message-date text-right"><date-time :at="message.created_at" /></div>
            </div>
        </div>
        <div class="mr-2 chat-photo-container">
            <img :src="`/uploads/users/${message.from.photo}`" class="ml-2 chat-photo">
        </div>
    </div>
</template>
<script>
    import LS from '../../services/localstorage'

    export default {
        name: 'chat-message',
        computed: {
            classes () {
                return {
                    'd-flex': true,
                    'p-3': true,
                    'chat-message': true,
                    'out': this.own,
                    'in': !this.own
                }
            },
            own () {
                return this.message.from.id !== LS.get('user').id
            }
        },
        props: {
            message: {
                type: Object,
                required: true
            }
        }
    }
</script>
<style scoped>
    .chat-message.out {
        justify-content: flex-end;
    }
    .chat-photo {
        border-radius: 50%;
        width: 40px;
        height: 40px;
    }
</style>